"use client"
import { useState } from 'react';

const BookingForm = () => {
  const [formData, setFormData] = useState({
    code: '',
    datetime: '',
    size: '',
    quantity: 1,
    customer: '',
    sale: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const response = await fetch('/api/bookings', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });
      if (!response.ok) {
        throw new Error('Failed to create booking');
      }
      alert('Booking created successfully!');
    } catch (error) {
      if (error instanceof Error) {
        alert(error.message);
      }
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <input name="code" value={formData.code} onChange={handleChange} placeholder="Code" required />
      <input type="datetime-local" name="datetime" value={formData.datetime} onChange={handleChange} required />
      <input name="size" value={formData.size} onChange={handleChange} placeholder="Size" required />
      <input type="number" name="quantity" value={formData.quantity} onChange={handleChange} min="1" required />
      <input name="customer" value={formData.customer} onChange={handleChange} placeholder="Customer Name" required />
      <input name="sale" value={formData.sale} onChange={handleChange} placeholder="Sale Name" required />
      <button type="submit">Create Booking</button>
    </form>
  );
};

export default BookingForm;